* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif;
}

/* GENERAL----------------------------------------- */
body {
  color: #333;
}
li {
  list-style: none;
}
.wrapper {
  min-height: 100vh;
}
#circle2 {
  position: absolute;
  transform: translate(-50%, -50%);
  height: 30px;
  width: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  transition: 90ms ease;
  background-color: transparent;
  border: 1px solid grey;
  color: white;
  z-index: 3;
}
.logo {
  font-size: 1.5rem;
}
.preloader {
  display: none;
}
input {
  -webkit-appearance: none;
}
/* General ENd ------------------*/

/* Header ------------------------------------------ */
header {
  background-color: white;
  cursor: default;
  padding: 0 3rem;
}
.hero {
  padding-top: 1rem;
  max-width: 1200px;
  margin: auto;
}
.hero-text {
  font-size: 8rem;
  margin-bottom: 1rem;
  -webkit-text-fill-color: white; /* Will override color (regardless of order) */
  -webkit-text-stroke: 1.4px black;
  text-transform: uppercase;
  font-family: "Work Sans", sans-serif !important;
}
/* HeaderEnd ------------------------*/

/* navigation---------------------------------------------  */
nav {
  padding: 2rem 1rem;
  display: flex;
  justify-content: space-between;
  font-size: large;
}
a {
  color: black;
  text-decoration: none;
}
.navlinks {
  display: flex;
  gap: 3rem;
  flex-direction: row;
}
.hamburger-lines {
  display: none;
}
/* navigation ends----------------  */
/*footer */
footer {
  padding: 1rem 0rem;
  margin-top: 2rem;
  background-color: whitesmoke;
  text-align: center;
}

.footer-basic {
  color: #4b4c4d;
}

.footer-basic ul {
  padding: 0;
  list-style: none;
  text-align: center;
  font-size: 18px;
  line-height: 1.6;
  margin-bottom: 0;
}

.footer-basic li {
  padding: 0 10px;
}

.footer-basic ul a {
  color: inherit;
  text-decoration: none;
  opacity: 0.8;
}

.footer-basic ul a:hover {
  opacity: 1;
}

.footer-basic .social {
  text-align: center;
  padding-bottom: 25px;
}

.footer-basic .social > a {
  font-size: 24px;
  width: 40px;
  height: 40px;
  line-height: 40px;
  display: inline-block;
  text-align: center;
  border-radius: 50%;
  border: 1px solid #ccc;
  margin: 0 8px;
  color: inherit;
  opacity: 0.75;
}

.footer-basic .social > a:hover {
  opacity: 0.9;
}
.footer-basic .copyright {
  margin-top: 15px;
  text-align: center;
  font-size: 13px;
  color: #aaa;
  margin-bottom: 0;
}
.list-inline {
  display: flex;
  justify-content: center;
}
.hamburger {
  background-color: white;
  border: none;
  color: black;
  font-size: 20px;
}

/*footer  end*/

/* animations */
textarea {
  user-scalable: no;
}
@keyframes rotate {
  0% {
    transform: rotate(-0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes pop {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@media screen and (-webkit-min-device-pixel-ratio: 0) {
  input[type="submit"] {
    border-radius: 0;
  }
  input[type="text"] {
    border-radius: 0;
  }
}
@media screen and (max-width: 768px) {
  nav {
    padding: 1rem;
  }
  header {
    padding: 1rem;
  }

  .hamburger-lines {
    display: block;
    z-index: 4;
  }
  .hero-text {
    font-size: 3.5rem;
  }
  .hero span {
    margin-bottom: 3rem !important;
  }
  .navlinks {
    display: none;
    background-color: white;
    flex-direction: column;
    justify-content: center;
    gap: 2rem;
    position: fixed;
    width: 100%;
    top: 0;
    align-items: center;
    left: 0;
    height: 100vh;
    z-index: 2;
    font-size: x-large;
  }
  .open {
    display: flex;
  }
  #x {
    z-index: 3;
    position: fixed;
    top: 2rem;
    right: 1rem;
  }
}
